    '외유내강(外柔內剛)한 성품을 지녔으며 강한 의지로 초지일관(初志一貫) 노력하는 수리이다. 아무리 힘든 고난(苦難)과 역경(逆境)이 닥쳐도 강인한 정신력으로 이겨내며 대업을 성취한다. 반면 신경이 날카롭고 독단적(獨斷的)인 면이 있어 주변 사람과 불화할 수 있는 요소도 지니고 있다. 의지가 강건하여 독립적으로 수행하려는 신념이 강하고 노력한 만큼의 성과를 얻어 부귀와 명예를 함께 얻을 수 있는 길한 수리이다.',
    '매사가 순조롭고 스스로 노력하고 개척(開拓)하는 일마다 좋은 결과를 맺게 되는 길한 수리이다. 사회적 기반을 닦고 대성하여 주변으로부터 두터운 신망(信望)을 얻는다. 사고력이 깊으며 성실하며 자립심이 강하니 한 번 목적한 바는 끝까지 이루고야 마는 진취적(進取的)인 기상이 있으니 가문을 일으켜 번창하게 하는 대길수이다. 부부가 해로하고 자녀가 번창하며 하는 일마다 순조로워 번영(繁英)을 이루게하는 길한 수리이다.',
